import React, { Component } from 'react'
import { ArcherElement } from 'react-archer';
import { shopEnums } from './graph';

export default class mapTile extends Component {
  
    
  render() {
    let imgSrc;
    if(this.props.img) {
      // In case of external images by URL
      if(this.props.img.startsWith("http")) {
        imgSrc = this.props.img
      }
      // In case of local file
      else {
        imgSrc = "mapimgs/" + this.props.img;
      }
    }
    // Code for showing the loot and shops data
    // Old shop list icon:
    let shopsJSX = [<> Shops: </>]
    shopsJSX = 
      this.props.shops && shopsJSX.concat(this.props.shops.map((shopType) => {
      switch(shopType) {
        case shopEnums.food:
          return <div className='shop'><img src="https://deadcells.wiki.gg/images/f/f7/Major-carni-chicken-96x96.png"></img>,</div>
          break;
        case shopEnums.skill:
          return <div className='shop'><img src="https://deadcells.wiki.gg/images/3/33/Double_Crossb-o-matic_Icon.png"></img>,</div>
          break;
        case shopEnums.weapon:
            return <div className='shop'><img src="https://deadcells.wiki.gg/images/2/26/Rusty_Sword_Icon.png"></img>,</div>
            break;
        case shopEnums.both:
          return <div className='shop'><img src="https://deadcells.wiki.gg/images/a/a6/Restock_Icon.png"></img>,</div>
        default:
          return <></>
      }
    }));

    // tf am i even doing here this makes no sense but if it works it works i guess
    let chestsJSX = <>
    {this.props.chests && <div><img src="https://deadcells.wiki.gg/images/5/54/Treasurechest_Closed.png"></img>:{this.props.chests || ""},</div> || ""}
    {this.props.cursedChests && <div><img src="https://deadcells.wiki.gg/images/0/0a/CursedChest_Open.png"></img>:{this.props.cursedChests || ""},</div> || ""}
    {<div>Cursed chest ch.: {(this.props.cursedChestPct + "%") || ""}</div>}
    </>
    
    // <img src={imgSrc} alt={this.props.name} />
    return (
      <div onClick={() => this.props.highlightBiome(this.props.index)}>
        <ArcherElement
        id={this.props.index}
        relations={this.props.relations}
        
        >
          
            <div id={this.props.index} className={`mapTile ${this.props.highlighted ? "mapTileHighlighted" : ""}`} style={{
              backgroundColor: "#ddd",
              top: this.props.style.marginTop,
              left: this.props.style.marginLeft,
              visibility: this.props.visibility
              }}
              
            
              >
                <div className="mapImgContainer" style={{
                  backgroundImage: `url("${imgSrc}")`
                }}
                >
                  
                </div>
                <div className="mapName"><div>{this.props.name}</div></div>
              <div className="mapDetails">
                <div>
                  {this.props.powerScrolls && <>
                    <img src="powerScroll.png" alt="Power Scroll"></img> 
                    {this.props.powerScrolls}
                  </> || ""}
                </div>
                <div>
                  {this.props.dualScrolls && <>
                    <img src="dualScroll.png" alt="Dual Scroll"></img> 
                    {this.props.dualScrolls}
                  </> || ""} 
                </div>
                <div>
                  {
                    this.props.scrollFragments && 
                    <>
                      , Fragments: {this.props.scrollFragments}
                    </>
                    || ""
                  }
                </div>
              </div>
              <div className="lootAndShops">
                  
                  {shopsJSX || ""}
                  {chestsJSX || ""}
              </div>
              </div>
            
          
        </ArcherElement>
      </div>
    )
  }
}
